body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #575656;
  height: 100vh;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 5px;
  background-color: rgb(24, 24, 24);
  padding-bottom: 15px;
  border-bottom: 1px solid rgb(0, 0, 0);
  justify-content: center;
}

header img {
  width: 100px;
  height: auto;
  padding: 5px;
  margin: 10px;
  margin-bottom: 0px;
}

header input {
  box-sizing: border-box;
  height: 35px;
  margin-top: 10px;
  margin-right: 10px;
  border-radius: 35px;
  border: none;
  background-color: #f3f3f3;
  padding-left: 15px;
  flex: 1;
}

input:focus-visible {
  border: 0.5px solid gray;
}

input:focus-within,
input:hover {
  border: 0.5px solid gray;
  outline: none;
}

nav ul {
  user-select: none;
  list-style-type: none;
  margin: 0px;
  margin-top: 10px;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: auto;
  padding: 3px;
}

nav ul li a:hover {
  background-color: #363636;
}

nav ul li a:active {
  background-color: #555555;
}

nav ul li a {
  padding: 5px;
  border-radius: 4px;
  color: rgb(207, 206, 206);
  text-decoration: none;
  font-size: 12px;
}
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.a-selected {
  background-color: #555555;
}
.outdiv {
  overflow: auto;
  height: 100%;
  background-color: #363636;
  margin: 5px;
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  color: white;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.productos-container,
.card {
  margin: 5px;
  height: fit-content;
  border-radius: 10px;
  overflow: hidden;
}

.productos-container .settings {
  top: 10px;
}

.productos-container {
  width: 100%;
  min-height: 50%;
}

h3 {
  width: 100%;
  text-align: center;
}

.card {
  background-color: #181818;
  width: 350px;
  min-height: 160px;
  border-top: 1px solid gray;
  padding: 10px;
  box-shadow: 0px 3px 5px 0px rgb(36, 36, 36);
}

.settings {
  width: 20px;
  height: 20px;
  border-radius: 3px;
  padding: 5px;
  float: right;
  position: relative;
  display: flex;
  justify-content: center;
}

.settings:hover {
  background-color: #575656;
}
.settings:active {
  background-color: #333333;
}

.settings span {
  background-color: #fff;
  border-radius: 50%;
  display: block;
  height: 4px;
  position: absolute;
  width: 4px;
}

.settings span:nth-child(1) {
  top: 7px;
}
.settings span:nth-child(2) {
  top: 13px;
}
.settings span:nth-child(3) {
  top: 19px;
}

.dropdown-content {
  position: absolute;
  right: 0;
  margin-top: 25px;
  padding: 5px;
  display: flex;
  flex-direction: column;
  background-color: #5b5b5b;
  border-radius: 5px;
  box-shadow: 0px 4px 5px 0px #121212;
  min-width: 150px;
}
.dropdown-content a {
  color: white;
  text-transform: capitalize;
  text-decoration: none;
  font-size: 15px;
  padding: 12px;
}
.dropdown-content a:hover {
  background-color: #777777;
  border-radius: 5px;
}
.dropdown-content a:active {
  background-color: #686767;
}
.dropdown-content p {
  margin: auto;
  margin-top: 10px;
  margin-bottom: 8px;
}

h2 {
  font-size: 20px;
  font-family: sans-serif;
  padding-top: 15px;
  padding-bottom: 15px;
  margin: 0px;
  cursor: pointer;
  user-select: none;
  transition: text-shadow 0.5s ease;
}

.linear-productos-header {
  display: flex;
  justify-content: space-between;
}

.linear-productos-header h2 {
  margin-left: 2px;
}

.back-btn {
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 30px;
  height: 30px;
}

.back-btn:hover {
  background-color: #ffffff25;
}

.back-btn:active {
  background-color: #04040424;
}

.productos-container h2 {
  cursor: default;
}

.card h2:hover {
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
}

.unidad {
  min-width: 20px;
}

h2,
p {
  margin-inline: 15px;
  color: white;
}

.card p {
  font-size: 14px;
  color: #d3d3d3;
  line-height: 1.2rem;
  max-height: 2.4rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
}

.tipo {
  margin-bottom: 0px;
}

.productos {
  margin-top: 0px;
}

p strong {
  color: white;
}

.fotos {
  display: flex;
  width: 100%;
  justify-content: center;
}

.product-preview-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
  font-size: 12px;
  height: 100px;
}

.product-preview-container img {
  height: -webkit-fill-available;
  object-fit: cover;
}

.fotos .product-preview-container,
.fotos img {
  user-select: none;
  width: inherit;
  border-radius: 10px;
}

.loading {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  animation: spin 2s linear infinite;
  margin: auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.producto-header-div {
  display: inline-flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

.precios-card .product-preview-container {
  width: 100%;
  height: 250px;
  overflow: hidden;
  margin: 0px;
}
.precios-card img {
  width: 350px;
  height: 250px;
  object-fit: cover;
}

.precios-card .precio {
  float: right;
  font-weight: bold;
  color: green;
  font-size: 18px;
}

.manager {
  margin-left: 10px;
}

.tabla {
  background-color: #101010;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.table-row {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.table-cell {
  display: table-cell;
}

.precios-top {
  text-align: center;
  font-size: 13px;
  margin: 15px;
}
